<template>
  <main-layout menuActiveIndex="popup">
    <h3>Popup </h3>
    <fish-card>
      <fish-button @click="popupHandler"></fish-button>
      <fish-button @click="popup2Handler">2</fish-button>
      <div slot="footer">
        <pre v-highlightjs><code class="html">&lt;fish-button @click=&quot;popupHandler&quot;&gt;&lt;/fish-button&gt;
&lt;fish-button @click=&quot;popup2Handler&quot;&gt;2&lt;/fish-button&gt;</code></pre><br/>
        <pre v-highlightjs><code class="javascript">popupHandler (event) {
  popup.confirm(event, '', () => {
    console.log('OK....')
  })
},
popup2Handler (event) {
  popup.confirm(event, '', () => {
    console.log('OK....')
  }, '', '')
}</code></pre>
      </div>
    </fish-card>

    <h3>Popup API</h3>
    <ul class="api">
      <li><code>popup.confirm: (event, html, okFunc, okText = 'Yes', cancelText = 'No')</code></li>
    </ul>

    <h3></h3>
    <div class="fish table attributes">
      <table style="table-layout: auto;">
        <thead>
        <tr>
          <th v-for="column in api_columns">{{column}}</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in api_data">
          <td v-for="v in item" v-html="v"></td>
        </tr>
        </tbody>
      </table>
    </div>
  </main-layout>
</template>
<script>
  import MainLayout from './MainLayout.vue'

  export default {
    components: {
      MainLayout
    },
    data () {
      return {
        api_columns: ['', '', '', ''],
        api_data: [
          ['event', '', 'Event', '-'],
          ['html', '', 'String', '-'],
          ['okFunc', '', 'Function()', '-'],
          ['okText', 'ok', 'String', 'Yes'],
          ['cancelText', 'cancel', 'String', 'No']
        ]
      }
    },
    methods: {
      popupHandler (event) {
        this.$popup.confirm(event, '', () => {
          console.log('OK....')
        })
      },
      popup2Handler (event) {
        this.$popup.confirm(event, '', () => {
          console.log('OK....')
        }, '', '')
      }
    }
  }
</script>



