<template>
  <main-layout menuActiveIndex="pagination">
    <h3>Pagination </h3>
    <fish-card>
      <fish-pagination :total="500" :current="page" @change="(current) => page = current"></fish-pagination>
      <footer slot="footer">
        <pre v-highlightjs><code class="html">&lt;fish-pagination :total=&quot;500&quot; :current=&quot;page&quot; @change=&quot;(current) =&gt; page = current&quot;&gt;&lt;/fish-pagination&gt;</code></pre>
      </footer>
    </fish-card>

    <h3>Pagination Attributes</h3>
    <div class="fish table attributes">
      <table style="table-layout: auto;">
        <thead>
        <tr>
          <th v-for="column in api_columns">{{column}}</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in api_data">
          <td v-for="v in item" v-html="v"></td>
        </tr>
        </tbody>
      </table>
    </div>

    <h3>Pagination Events</h3>
    <div class="fish table attributes">
      <table style="table-layout: auto;">
        <thead>
        <tr>
          <th v-for="column in event_columns">{{column}}</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in event_data">
          <td v-for="v in item" v-html="v"></td>
        </tr>
        </tbody>
      </table>
    </div>
  </main-layout>
</template>
<script>
  import MainLayout from './MainLayout.vue'

  export default {
    components: {
      MainLayout
    },
    data () {
      return {
        page: 1,
        api_columns: ['', '', '', ''],
        api_data: [
          ['total', '', 'Number', '-'],
          ['current', '', 'Number', '1'],
          ['rows', '', 'Number', '10'],
          ['totalRender', '', 'Function(total, range)', '-']
        ],
        event_columns: ['', '', ''],
        event_data: [
          ['change', 'current ', ' <code>current</code>']
        ]
      }
    }
  }
</script>



