<template>
  <main-layout menuActiveIndex="timepicker">
    <h3>TimePicker </h3>
    <fish-card>
      <fish-time-picker v-model="timePickerValue"></fish-time-picker> [{{timePickerValue}}]
      <div slot="footer">
        <pre v-highlightjs><code class="html">&lt;fish-time-picker v-model=&quot;timePickerValue&quot;&gt;&lt;/fish-time-picker&gt;</code></pre>
      </div>
    </fish-card>

    <h3>TimePicker Attributes</h3>
    <div class="fish table attributes">
      <table style="table-layout: auto;">
        <thead>
        <tr>
          <th v-for="column in api_columns">{{column}}</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in api_data">
          <td v-for="v in item" v-html="v"></td>
        </tr>
        </tbody>
      </table>
    </div>

    <h3>TimePicker Events</h3>
    <div class="fish table attributes">
      <table style="table-layout: auto;">
        <thead>
        <tr>
          <th v-for="column in event_columns">{{column}}</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in event_data">
          <td v-for="v in item" v-html="v"></td>
        </tr>
        </tbody>
      </table>
    </div>
  </main-layout>
</template>
<script>
  import MainLayout from './MainLayout.vue'

  export default {
    components: {
      MainLayout
    },
    data () {
      return {
        timePickerValue: '',
        event_columns: ['', '', ''],
        event_data: [
          ['change(v)', '', 'v']
        ],
        api_columns: ['', '', '', ''],
        api_data: [
          ['value', 'v-model', 'String', '00:00:00'],
          ['hint', '', 'String', 'Please time']
        ]
      }
    }
  }
</script>



