<template>
  <main-layout menuActiveIndex="tabs">
    <h3>Tabs </h3>
    <fish-card>
      <fish-tabs value="role">
        <fish-tab-pane label="" index="user"></fish-tab-pane>
        <fish-tab-pane label="" index="role"></fish-tab-pane>
        <fish-tab-pane label="" index="config"></fish-tab-pane>
      </fish-tabs><br/>
      <fish-tabs type="card" value="user" @tab-change="tabChangeHandler">
        <fish-tab-pane label="" index="user"></fish-tab-pane>
        <fish-tab-pane label="" index="role"></fish-tab-pane>
        <fish-tab-pane label="" index="config"></fish-tab-pane>
      </fish-tabs>
      <div slot="footer">
        <pre v-highlightjs><code class="html">&lt;fish-tabs value=&quot;role&quot;&gt;
  &lt;fish-tab-pane label=&quot;&quot; index=&quot;user&quot;&gt;&lt;/fish-tab-pane&gt;
  &lt;fish-tab-pane label=&quot;&quot; index=&quot;role&quot;&gt;&lt;/fish-tab-pane&gt;
  &lt;fish-tab-pane label=&quot;&quot; index=&quot;config&quot;&gt;&lt;/fish-tab-pane&gt;
&lt;/fish-tabs&gt;&lt;br/&gt;
&lt;fish-tabs type=&quot;card&quot; value=&quot;user&quot; @tab-change=&quot;tabChangeHandler&quot;&gt;
  &lt;fish-tab-pane label=&quot;&quot; index=&quot;user&quot;&gt;&lt;/fish-tab-pane&gt;
  &lt;fish-tab-pane label=&quot;&quot; index=&quot;role&quot;&gt;&lt;/fish-tab-pane&gt;
  &lt;fish-tab-pane label=&quot;&quot; index=&quot;config&quot;&gt;&lt;/fish-tab-pane&gt;
&lt;/fish-tabs&gt;</code></pre><br/>
        <pre v-highlightjs><code class="javascript">tabChangeHandler (index) {
  console.log('tab.index: ', index)
}</code></pre>
      </div>
    </fish-card>

    <h3>Tabs Attributes</h3>
    <div class="fish table attributes">
      <table style="table-layout: auto;">
        <thead>
        <tr>
          <th v-for="column in api_columns">{{column}}</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in api_data">
          <td v-for="v in item" v-html="v"></td>
        </tr>
        </tbody>
      </table>
    </div>

    <h3>Tabs Events</h3>
    <div class="fish table attributes">
      <table style="table-layout: auto;">
        <thead>
        <tr>
          <th v-for="column in event_columns">{{column}}</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in event_data">
          <td v-for="v in item" v-html="v"></td>
        </tr>
        </tbody>
      </table>
    </div>

    <h3>TabPanel Attributes</h3>
    <div class="fish table attributes">
      <table style="table-layout: auto;">
        <thead>
        <tr>
          <th v-for="column in api_columns">{{column}}</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in api_data2">
          <td v-for="v in item" v-html="v"></td>
        </tr>
        </tbody>
      </table>
    </div>
  </main-layout>
</template>
<script>
  import MainLayout from './MainLayout.vue'

  export default {
    components: {
      MainLayout
    },
    data () {
      return {
        event_columns: ['', '', ''],
        event_data: [
          ['tab-change(index)', 'tab', 'TabPanel.index <code>index</code>']
        ],
        api_columns: ['', '', '', ''],
        api_data: [
          ['value', '', 'String, Number', '-'],
          ['type', '<code>card</code> ', 'String', '-']
        ],
        api_data2: [
          ['label', 'tab', 'String', '-'],
          ['index', '', 'String', '-']
        ]
      }
    },
    methods: {
      tabChangeHandler (index) {
        console.log('tab.index: ', index)
      }
    }
  }
</script>



